// ** React Imports
import { useEffect, Fragment } from 'react'
import { useParams } from 'react-router-dom'


// ** Custom Components
import BreadCrumbs from '@components/breadcrumbs'
import ProductDetails from './ProductDetails'
// ** Third Party Components
import { Card, CardBody } from 'reactstrap'

// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'
import { getTaskDetail, deleteWishlistItem, addToWishlist, addToCart } from '../store/actions'

import '@styles/base/pages/app-ecommerce-details.scss'
import { param } from 'jquery'

const Details = () => {
  // ** Vars
  const { id } = useParams()
  const productId = id
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.invoice)
  // ** ComponentDidMount : Get product
  useEffect(() => {
    dispatch(getTaskDetail(productId))
  }, [])

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Jobs Details' breadCrumbParent='Jobs' breadCrumbActive='Details' />
      <div className='app-ecommerce-details d-flex justify-content-center'>
        {Object.keys(store.detail).length ? (
          <Card className="w-75">
            <CardBody>
              <ProductDetails
                dispatch={dispatch}
                data={store.detail[0]}
              />
            </CardBody>
          </Card>
        ) : null}
      </div>
    </Fragment>
  )
}

export default Details
